import React, { createContext, useContext, useState, useEffect } from 'react';
import { SupportedLanguage, SUPPORTED_LANGUAGES } from '../services/translationService';

// 
interface LanguageContextType {
  currentLanguage: SupportedLanguage;
  setLanguage: (lang: SupportedLanguage) => void;
  supportedLanguages: Array<{ code: SupportedLanguage; name: string }>;
}

// 
const LanguageContext = createContext<LanguageContextType | undefined>(undefined);

const LANGUAGE_STORAGE_KEY = 'app_language';

// 
interface LanguageProviderProps {
  children: React.ReactNode;
}

// 
export const LanguageProvider: React.FC<LanguageProviderProps> = ({ children }) => {
  // 
  const getInitialLanguage = (): SupportedLanguage => {
    const storedLang = localStorage.getItem(LANGUAGE_STORAGE_KEY);
    if (storedLang && Object.keys(SUPPORTED_LANGUAGES).includes(storedLang)) {
      return storedLang as SupportedLanguage;
    }

    // 
    const browserLang = navigator.language.split('-')[0];
    if (Object.keys(SUPPORTED_LANGUAGES).includes(browserLang)) {
      return browserLang as SupportedLanguage;
    }

    return 'zh'; // 
  };

  const [currentLanguage, setCurrentLanguage] = useState<SupportedLanguage>(getInitialLanguage());

  // 
  useEffect(() => {
    localStorage.setItem(LANGUAGE_STORAGE_KEY, currentLanguage);
  }, [currentLanguage]);

  const setLanguage = (lang: SupportedLanguage) => {
    setCurrentLanguage(lang);
  };

  const supportedLanguages = Object.entries(SUPPORTED_LANGUAGES).map(([code, name]) => ({
    code: code as SupportedLanguage,
    name
  }));

  return (
    <LanguageContext.Provider value={{ currentLanguage, setLanguage, supportedLanguages }}>
      {children}
    </LanguageContext.Provider>
  );
};

// Hook
export const useLanguage = () => {
  const context = useContext(LanguageContext);
  if (context === undefined) {
    throw new Error('useLanguage must be used within a LanguageProvider');
  }
  return context;
};

export default LanguageContext; 