// 
const preloadImage = (url: string): Promise<void> => {
  if (!url) {
    console.log(' URL');
    return Promise.resolve(); // Promise
  }

  console.log(':', url);
  
  return new Promise((resolve) => { // reject
    const img = new Image();
    img.onload = () => {
      console.log(':', url);
      resolve();
    };
    img.onerror = (error) => {
      console.error(':', url, error);
      // rejectresolvePromise
      resolve(); // PromisePromise.all
    };
    img.src = url;
  });
};

// 
const ScientistCard = lazy(() => import('../components/ScientistCard'));

const WorshipButton: React.FC = () => {
  const [isHovered, setIsHovered] = useState(false);
  const [showPopup, setShowPopup] = useState(false);
  const [isExpanded, setIsExpanded] = useState(false);
  const [selectedScientist, setSelectedScientist] = useState<Scientist | null>(null);
  const [preloadedImages, setPreloadedImages] = useState<Set<string>>(new Set());
  const [scientists, setScientists] = useState<Scientist[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const buttonRef = useRef<HTMLDivElement>(null);
  const preloadTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const [showEChurch, setShowEChurch] = useState(false);
  const [worshipCount, setWorshipCount] = useState(0);

  // 
  useEffect(() => {
    const fetchScientists = async (retryCount = 0) => {
      setLoading(true);
      setError(null);
      
      try {
        console.log('...');
        console.log('API URL:', process.env.REACT_APP_API_URL);
        
        const response = await fetch(`${process.env.REACT_APP_API_URL}/api/scientists`, {
          method: 'GET',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        });
        
        if (!response.ok) {
          console.error(`API: ${response.status} ${response.statusText}`);
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const data = await response.json();
        console.log(':', data.length);
        
        //  URL - 
        const processedData = data.map((scientist: Scientist) => {
          // 
          const fallbackImage = `/images/${scientist.name ? scientist.name.toLowerCase() : 'einstein'}.jpg`;
          const fallbackThumb = `/images/${scientist.name ? scientist.name.toLowerCase() : 'einstein'}-thumb.jpg`;
          
          return {
            ...scientist,
            // URL
            image: scientist.image?.startsWith('http') 
              ? scientist.image 
              : `${process.env.REACT_APP_API_URL}${scientist.image}`,
            // 
            fallbackImage: fallbackImage,
            // 
            thumbnail: scientist.thumbnail?.startsWith('http')
              ? scientist.thumbnail
              : scientist.thumbnail 
                ? `${process.env.REACT_APP_API_URL}${scientist.thumbnail}`
                : scientist.image?.startsWith('http')
                  ? scientist.image
                  : `${process.env.REACT_APP_API_URL}${scientist.image}`,
            fallbackThumbnail: fallbackThumb
          };
        });
        setScientists(processedData);
        setError(null);
      } catch (error) {
        console.error(':', error);
        setError('');
        
        // 3
        if (retryCount < 3 && error instanceof Error && 
            (error.message.includes('fetch') || error.message.includes('network'))) {
          console.log(`3 (${retryCount + 1}/3)...`);
          setTimeout(() => fetchScientists(retryCount + 1), 3000);
        }
      } finally {
        setLoading(false);
      }
    };

    fetchScientists();
    
    // 
    (window as any).retryFetchingScientists = () => fetchScientists();
    
    return () => {
      // 
      delete (window as any).retryFetchingScientists;
    };
  }, []);

  // 
  useEffect(() => {
    const fetchWorshipCount = async () => {
      try {
        const response = await fetch(`${process.env.REACT_APP_API_URL}/api/worship/count`);
        if (response.ok) {
          const data = await response.json();
          setWorshipCount(data.count);
        }
      } catch (error) {
        console.error(':', error);
      }
    };

    fetchWorshipCount();
  }, []);

  // 
  const preloadAllImages = useCallback(async () => {
    try {
      if (!scientists || scientists.length === 0) {
        console.log('');
        return;
      }

      console.log('...');
      
      // Promise.all
      const allImages = [
        ...scientists.filter(s => s.image).map(s => s.image),
        ...scientists.filter(s => s.thumbnail).map(s => s.thumbnail)
      ].filter(url => url && !preloadedImages.has(url));
      
      console.log(`: ${allImages.length}`);
      
      for (const url of allImages) {
        try {
          await preloadImage(url);
          // 
          setPreloadedImages(prev => new Set([...Array.from(prev), url]));
        } catch (err) {
          console.error(`: ${url}`, err);
          // 
        }
      }
      
      console.log('');
    } catch (error) {
      console.error(':', error);
    }
  }, [scientists, preloadedImages]);

  // 
  const handleMouseMove = useCallback((e: MouseEvent) => {
    if (!buttonRef.current) return;
    
    const rect = buttonRef.current.getBoundingClientRect();
    const buttonCenterX = rect.left + rect.width / 2;
    const buttonCenterY = rect.top + rect.height / 2;
    
    const distanceX = Math.abs(e.clientX - buttonCenterX);
    const distanceY = Math.abs(e.clientY - buttonCenterY);
    
    const isHovering = distanceX < 150 && distanceY < 150;
    setIsHovered(isHovering);

    // 
    if (isHovering && !preloadTimeoutRef.current) {
      preloadTimeoutRef.current = setTimeout(() => {
        // 
        void import('../components/ScientistCard').catch(err => {
          console.error(':', err);
        });
        // 
        preloadAllImages();
      }, 200);
    } else if (!isHovering && preloadTimeoutRef.current) {
      clearTimeout(preloadTimeoutRef.current);
      preloadTimeoutRef.current = null;
    }
  }, [preloadAllImages]);

  const handleClick = async () => {
    if (loading || error) return;

    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL}/api/worship`, {
        method: 'POST'
      });
      
      if (response.ok) {
        const data = await response.json();
        setWorshipCount(data.count);
        setShowPopup(true);
        setIsExpanded(true);
        setShowEChurch(true);
      } else {
        throw new Error('');
      }
    } catch (error) {
      console.error(':', error);
      setError('error');
    }
  };

  // 
  const handleWorshipComplete = async () => {
    try {
      // 
      const response = await fetch(`${process.env.REACT_APP_API_URL}/api/scientists/worship`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          scientistId: selectedScientist?._id
        })
      });
      
      if (response.ok) {
        const data = await response.json();
        setWorshipCount(data.totalWorshipCount || worshipCount + 1);
      }
      
      // 
      const showChurchChance = Math.random();
      if (showChurchChance < 0.2) { // 20% 
        setShowEChurch(true);
      } else {
        closePopup();
      }
    } catch (error) {
      console.error(':', error);
      closePopup();
    }
  };

  // 
  const closePopup = () => {
    setShowPopup(false);
    setTimeout(() => {
      setSelectedScientist(null);
      setShowEChurch(false);
    }, 300);
  };

  // 
  useEffect(() => {
    window.addEventListener('mousemove', handleMouseMove);
    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
      if (preloadTimeoutRef.current) {
        clearTimeout(preloadTimeoutRef.current);
      }
    };
  }, [handleMouseMove]);

  return (
    <ButtonContainer
      ref={buttonRef}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <StyledButton
        onClick={handleClick}
        $isHovered={isHovered}
        $isExpanded={isExpanded}
        disabled={loading}
      >
        {loading ? (
          <SmartText text="..." />
        ) : error ? (
          <SmartText text="" />
        ) : (
          <SmartText text={` (${worshipCount})`} />
        )}
      </StyledButton>

      {/*  */}
      <PopupOverlay $isVisible={showPopup}>
        {showPopup && <BuddhaGlow />}
        
        {selectedScientist && showPopup && !showEChurch && (
          <React.Suspense fallback={<div style={{ color: 'white' }}><SmartText text="..." /></div>}>
            <ScientistCard
              scientist={selectedScientist}
              onClose={closePopup}
              onWorshipComplete={handleWorshipComplete}
            />
          </React.Suspense>
        )}
        
        {showEChurch && (
          <EChurch onClose={() => {
            setShowEChurch(false);
            closePopup();
          }} />
        )}
      </PopupOverlay>
    </ButtonContainer>
  );
};

export default WorshipButton; 
