import React, { useEffect, useState } from 'react';
import { translationService, SupportedLanguage } from '../services/translationService';
import { useLanguage } from '../contexts/LanguageContext';

export type TextContent = {
  [K in SupportedLanguage]?: string;
};

interface SmartTextProps {
  text: string | TextContent;
  className?: string;
  tag?: keyof JSX.IntrinsicElements;
  dangerouslySetInnerHTML?: boolean;
}

export const SmartText: React.FC<SmartTextProps> = ({
  text,
  className = '',
  tag: Tag = 'span',
  dangerouslySetInnerHTML = false,
}) => {
  const { currentLanguage } = useLanguage();
  const [translatedText, setTranslatedText] = useState<string>(typeof text === 'string' ? text : text.zh || '');
  const [isLoading, setIsLoading] = useState<boolean>(false);

  useEffect(() => {
    let isMounted = true;

    const translateText = async () => {
      // 
      if (!text) {
        setTranslatedText('');
        return;
      }

      // 
      if (typeof text === 'string') {
        setTranslatedText(text);
        return;
      }

      // 
      const textContent = text as TextContent;
      if (currentLanguage === 'zh') {
        setTranslatedText(textContent.zh || '');
        return;
      }

      // 
      if (textContent[currentLanguage]) {
        setTranslatedText(textContent[currentLanguage]!);
        return;
      }

      // 
      setIsLoading(true);
      try {
        const result = await translationService.translate(textContent.zh || '', currentLanguage);
        if (isMounted) {
          setTranslatedText(result);
        }
      } catch (error) {
        console.error('Translation error:', error);
        if (isMounted) {
          setTranslatedText(textContent.zh || ''); // 
        }
      } finally {
        if (isMounted) {
          setIsLoading(false);
        }
      }
    };

    translateText();

    return () => {
      isMounted = false;
    };
  }, [text, currentLanguage]);

  const commonProps = {
    className: `smart-text ${className} ${isLoading ? 'loading' : ''}`,
  };

  if (dangerouslySetInnerHTML) {
    return <Tag {...commonProps} dangerouslySetInnerHTML={{ __html: translatedText }} />;
  }

  return <Tag {...commonProps}>{translatedText}</Tag>;
};

export default SmartText;