interface TranslationResponse {
  translatedText: string;
}

interface BatchTranslationResponse {
  translations: Array<{
    original: string;
    translated: string;
  }>;
}

export type SupportedLanguage = 'zh' | 'en' | 'ja' | 'ko' | 'ru' | 'fr' | 'de' | 'es';

export const SUPPORTED_LANGUAGES: Record<SupportedLanguage, string> = {
  'zh': '',
  'en': 'English',
  'ja': '',
  'ko': '',
  'ru': '',
  'fr': 'Franais',
  'de': 'Deutsch',
  'es': 'Espaol'
};

class TranslationService {
  private apiBaseUrl: string = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

  async translate(text: string, targetLang: SupportedLanguage): Promise<string> {
    try {
      if (!text) return text;

      const response = await fetch(`${this.apiBaseUrl}/translations/translate`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          text,
          targetLang,
        }),
      });

      if (!response.ok) {
        throw new Error('Translation request failed');
      }

      const data: TranslationResponse = await response.json();
      return data.translatedText;
    } catch (error) {
      console.error('Translation error:', error);
      return text;
    }
  }

  async translateBatch(texts: string[], targetLang: SupportedLanguage): Promise<Array<{ original: string; translated: string }>> {
    try {
      if (!texts.length) return [];
      
      const response = await fetch(`${this.apiBaseUrl}/translations/translate-batch`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          texts,
          targetLang,
        }),
      });

      if (!response.ok) {
        throw new Error('Batch translation request failed');
      }

      const data: BatchTranslationResponse = await response.json();
      return data.translations;
    } catch (error) {
      console.error('Batch translation error:', error);
      return texts.map(text => ({ original: text, translated: text }));
    }
  }

  getSupportedLanguages(): Array<{ code: SupportedLanguage; name: string }> {
    return Object.entries(SUPPORTED_LANGUAGES).map(([code, name]) => ({
      code: code as SupportedLanguage,
      name
    }));
  }
}

export const translationService = new TranslationService(); 