import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import styled from 'styled-components';
import axios from 'axios';
import SmartText from '../components/SmartText';
import { newsDetailTexts } from '../translations/texts';
import { useLanguage } from '../contexts/LanguageContext';

interface NewsPost {
  title: string;
  content: string;
  date: string;
  author: {
    name: string;
  };
}

const Container = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const Title = styled.h1`
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
`;

const Meta = styled.div`
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
`;

const Content = styled.div`
  font-size: 1.1rem;
  line-height: 1.8;
  color: #444;
  
  img {
    max-width: 100%;
    height: auto;
    margin: 20px 0;
  }
  
  p {
    margin-bottom: 20px;
  }
`;

const Loading = styled.div`
  text-align: center;
  padding: 40px;
  font-size: 1.2rem;
  color: #666;
`;

const ErrorMessage = styled.div`
  text-align: center;
  padding: 40px;
  color: #dc3545;
  font-size: 1.2rem;
`;

const NewsDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const [post, setPost] = useState<NewsPost | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { currentLanguage } = useLanguage();

  useEffect(() => {
    const fetchPost = async () => {
      try {
        const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/wordpress/posts/${id}`);
        setPost(response.data);
      } catch (err) {
        setError('fetchFailed');
        console.error('Error fetching post:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchPost();
  }, [id]);

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString(currentLanguage === 'zh' ? 'zh-CN' : currentLanguage === 'ja' ? 'ja-JP' : 'en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };

  if (loading) {
    return <Loading><SmartText text={newsDetailTexts.loading} /></Loading>;
  }

  if (error) {
    return <ErrorMessage><SmartText text={newsDetailTexts.error[error as keyof typeof newsDetailTexts.error]} /></ErrorMessage>;
  }

  if (!post) {
    return <ErrorMessage><SmartText text={newsDetailTexts.error.notFound} /></ErrorMessage>;
  }

  return (
    <Container>
      <Title dangerouslySetInnerHTML={{ __html: post.title }} />
      <Meta>
        <span>
          <SmartText text={newsDetailTexts.meta.author} />: {post.author.name}
        </span>
        {' | '}
        <span>
          <SmartText text={newsDetailTexts.meta.publishDate} />: {formatDate(post.date)}
        </span>
      </Meta>
      <Content dangerouslySetInnerHTML={{ __html: post.content }} />
    </Container>
  );
};

export default NewsDetailPage; 